//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import mutations from './mutations';
//全局注册
Vue.use(Vuex);
//创建store对象
const store = new Vuex.Store({
    //状态数据
    state:{
        //主题分类
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'},
          ],
          //未登录更多菜单
          unLoginmoreMenus:{
            login:'登录'
          },
          //登录成功更多菜单
          LoginmoreMenus:{
            create:'发表新话题',
            center:'个人中心',
            msg:'消息通知',
            logout:'注销'
          },
          isLogined:false,//登录状态
          loginname:null,//用户名
          accesstoken:'',//用户登录的accesstoken
          isShowMoreMenus:false,//是否显示更多菜单
          author_id:''//登录时的id
    },
   
    //状态数据的计算属性
    getters:{
      moreMenus:function(state){
        if(state.isLogined){
          return state.LoginmoreMenus
        }
        return state.unLoginmoreMenus
      },
      getTopicDescByTab:function(state){
        return function(tab) {
          for(var i=0;i<state.topics.length;i++){
            if(state.topics[i].name==tab){
              return state.topics[i].desc
            }
          }
        }
      }
    },
    mutations,
    actions
});
//对外导出模块
export default store;


